*{
  margin: 0 auto;
  font-family:Verdana, Geneva, Tahoma, sans-serif
}
.ns{
  color: rgb(0, 183, 255);
  font-size: 1.5rem;
 position:absolute;
 height: 100%;
 width: 70%;
 display: flex;
 flex-direction: column;
 justify-content: space-between;
 align-items: center;
 padding: 10px 0;

}
.apli{
  background-image: url(assets/bg.webp);
  width: 100vw;
  height: 100vh;
  display: flex;
  padding-left: 20rem;
  

}
.body{
  background-image: url(assets/capitales.jpg);
  background-size: contain;
  background-repeat: no-repeat;
  height: 100vh;
  width: 100vw;
}
.container{
  font-size: 1/2;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows:  repeat(12, 1fr) ;
  color:  rgb(58, 58, 58);;
  text-align: center;
  font-weight: bold;
}
.miami{
  grid-column: 7/8;
  grid-row : 10/11
}
.seattle{
  grid-column: 2/3;
  grid-row : 2/3
}
.austin{
  grid-column: 4/5;
  grid-row : 9/10
}
.bemidji{
  grid-column: 5/6 ;
  grid-row: 3/4 ;
}